import React from 'react';
import Navbar from 'react-bootstrap/Navbar';

function logo() {
    return (
        <Navbar bg='dark' variant='dark'>
            <Navbar.Brand href="/Pathing-Finding/">
                <img
                    src='./logo.png'
                    width="50"
                    height="50"
                    className='d-inline-block'
                    variant='test'
                    alt='Logo'
                /> {' '}
                    Path-Finding
                </Navbar.Brand>
        </Navbar>
    )
}

export default logo
